.history {  
    box-sizing: border-box;
    width: 100%;
    height: 7rem;
    margin-top: 2%;
    border: 1px solid #000000; 
    background-color: #E6E6E6;
    border: solid 1px #363338;
    border-radius: 10px;       
    padding: 4px;
    font-weight: normal;
    overflow: scroll;  
    -ms-overflow-style: none;
    scrollbar-width: none;
    font-size: 1rem;
}

.history::-webkit-scrollbar {
    display: none;
}