* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  background-color: #EDF5F9;
  font-family: 'Lato';
  font-size: 16px;
}


html {
  margin: 0;
  background-color: #EDF5F9;
  font-family: 'Lato';
  font-size: 16px;
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

html::-webkit-scrollbar {
  display: none;
}

:focus {outline:none;}
::-moz-focus-inner {border:0;}

@media (max-width: 900px) {
  html {
    font-size: 14px;
  }
  body {
    font-size: 14px;
  }
}