

.container{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}


.title{
    text-align: center;
    color: #363338;
    text-decoration: underline;
    margin-bottom: 1.5rem;
    font-size: 2.3rem;
}


.left-side{
  position: relative;
 width: 60%;
 margin-top: 40px;
 min-width: 200px;
/* max-width: 800px; */
 /* margin-right: 50px; */
 flex-grow: 1;
}

.right-side{
  position: relative;
  /* width: 40%; */
  height: fit-content;
  min-width: 200px;
  /* max-width: 700px; */
  margin: 0, auto;
  margin-top: 40px;
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
}


@media (max-width: 2000px) { 
  .right-side{
    width: 40%;
  }
  .right-side-team{
    width: 80%;
  }
}

@media (max-width: 1100px) {
  .container {
    flex-flow: column-reverse;    
  }
  .left-side{
    height: 60%;
    width: 100%;
  }
  .right-side{
    width: 100%;
    /* position: fixed;
    bottom: 10px; */
  }
  .right-side-team{
    width: 100%;
  }
  .team-page-picture{
    visibility: hidden;
  }
}

.button-div{
  display: flex;
  flex-wrap: wrap;
  margin: 5%;
}


.team-page-button {     
  margin-bottom: 15px;
  margin-right: 15px;
  outline: none;
  width: 160px;
}
  

  .right-side-team{   
    /* width: 80%;   */
    height: fit-content;  
    /* margin-bottom: 50px; */
    /* margin-left: 20%; */
    background-color: #F7F7F2;
    border-radius: 22px;
    border: solid 1px #707070;
    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.1);
    font-size: 1.3rem;
    padding: 5px;
    
  }

  .team-page-picture{
    position: fixed;
    bottom: 0;
    right: 80px;
    padding: 1%;
  }

  .picture{
    z-index: 0;
  }